        channelCall: async function (json) {
            const appData = JSON.parse(json);
            console.log(appData);

            this.msg = "Wiat data";
            if (appData.name == "aeGetCurrentHeight") {
                this.aeGetCurrentHeight();
            }
            if (appData.name == "aeGenerateAccount") {
                this.aeGenerateAccount();
            }
            if (appData.name == "aeSetNodeUrl") {
                this.aeSetNodeUrl(appData.params.url);
            }
            if (appData.name == "aeBalance") {
                this.aeBalance(appData.params.address);
            }
            if (appData.name == "aeRestoreAccountMnemonic") {
                this.aeRestoreAccountMnemonic(appData.params.mnemonic);
            }
            if (appData.name == "aeRestoreAccountSecretKey") {
                this.aeRestoreAccountSecretKey(appData.params.secretKey);
            }
            if (appData.name == "aeSpend") {
                this.aeSpend(
                    appData.params.secretKey,
                    appData.params.receiveAddress,
                    appData.params.amount,
                    appData.params.payload
                );
            }
            if (appData.name == "aeAensUpdate") {
                this.aeAensUpdate(appData.params.secretKey, appData.params.name, appData.params.address);
            }
            if (appData.name == "aeAensClaim") {
                this.aeAensClaim(appData.params.secretKey, appData.params.name);
            }
            if (appData.name == "aeAensTransfer") {
                this.aeAensTransfer(
                    appData.params.secretKey,
                    appData.params.name,
                    appData.params.recipient
                );
            }
            if (appData.name == "aeAex9TokenBalance") {
                this.aeAex9TokenBalance(
                    appData.params.address,
                    appData.params.ctAddress
                );
            }
            if (appData.name == "aeAex9TokenTransfer") {
                this.aeAex9TokenTransfer(
                    appData.params.secretKey,
                    appData.params.toAddress,
                    appData.params.amount,
                    appData.params.ctAddress
                );
            }
            if (appData.name == "aeAex9TokenAllowance") {
                this.aeAex9TokenAllowance(
                    appData.params.ctAddress,
                    appData.params.address,
                    appData.params.forAddress
                );
            }
            if (appData.name == "aeAex9TokenCreateAllowance") {
                this.aeAex9TokenCreateAllowance(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.forAddress,
                    appData.params.amount
                );
            }
            if (appData.name == "aeVegasgetAggregatorUser") {
                this.aeVegasgetAggregatorUser(appData.params.ctAddress);
            }
            if (appData.name == "aeVegasAddMarket") {
                this.aeVegasAddMarket(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.content,
                    appData.params.sourceUrl,
                    appData.params.minAmount,
                    appData.params.overHeight,
                    appData.params.results
                );
            }

            if (appData.name == "aeVegasUpdateConfig") {
                this.aeVegasUpdateConfig(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.config
                );
            }
            if (appData.name == "aeVegasMarketDetail") {
                this.aeVegasMarketDetail(
                    appData.params.ctAddress,
                    appData.params.address,
                    appData.params.owner,
                    appData.params.marketId
                );
            }
            if (appData.name == "aeVegasMarketStart") {
                this.aeVegasMarketStart(
                    appData.params.ctAddress,
                    appData.params.address,
                    appData.params.isSelf
                );
            }
            if (appData.name == "aeVegasMarkeReferee") {
                this.aeVegasMarkeReferee(
                    appData.params.ctAddress,
                    appData.params.address
                );
            }
            if (appData.name == "aeVegasMarketRecords") {
                this.aeVegasMarketRecords(
                    appData.params.ctAddress,
                    appData.params.address
                );
            }
            if (appData.name == "aeVegasSubmitAnswer") {
                this.aeVegasSubmitAnswer(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.owner,
                    appData.params.marketId,
                    appData.params.selectIndex,
                    appData.params.amount
                );
            }
            if (appData.name == "aeVegasProvideAnswer") {
                this.aeVegasProvideAnswer(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.owner,
                    appData.params.marketId,
                    appData.params.selectIndex,
                    appData.params.marketType
                );
            }
            if (appData.name == "aeVegasStopMarket") {
                this.aeVegasStopMarket(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.owner,
                    appData.params.marketId
                );
            }
            if (appData.name == "aeVegasReceiveReward") {
                this.aeVegasReceiveReward(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.owner,
                    appData.params.marketId
                );
            }
            if (appData.name == "aeAensMarketPutName") {
                this.aeAensMarketPutName(
                    appData.params.secretKey,
                    appData.params.ctAddress,
                    appData.params.name,
                    appData.params.amount,
                    appData.params.height
                );
            }
            if (appData.name == "aeAensMarketGetNameMaxPrice") {
                this.aeAensMarketGetNameMaxPrice(
                    appData.params.ctAddress,
                    appData.params.name
                );
            }
            if (appData.name == "aeAensMarketIsCallerExists") {
                this.aeAensMarketIsCallerExists(
                    appData.params.ctAddress,
                    appData.params.address
