        aeSpend: async function (secretKey, receiveAddress, amount, payload) {
            try {
                const publicKey = getAddressFromPriv(secretKey);
                const senderAccount = new MemoryAccount({
                    keypair: {secretKey: secretKey, publicKey: publicKey},
                });
                await aeSdk.addAccount(senderAccount, {select: true});
                let spendTxResult = await aeSdk.spend(amount, receiveAddress, {
                    denomination: AE_AMOUNT_FORMATS.AE,
                    payload: Base64.decode(payload),
                });
                this.responseSucess("aeSpend", {hash: spendTxResult.hash});
            } catch (e) {
                this.responseError("aeSpend", e.message);
            }
        },

        aeAensUpdate: async function (secretKey, name, address) {
            try {
                let nameInfo = await aeSdk.api.getNameEntryByName(name);
                if (nameInfo == null) {
                    this.responseError("aeAensUpdate", "name is not exist");
                    return;
                }
                const publicKey = getAddressFromPriv(secretKey);
                const senderAccount = new MemoryAccount({
                    keypair: {secretKey: secretKey, publicKey: publicKey},
                });
                const pointers = {
                    account_pubkey: address,
                };
                await aeSdk.addAccount(senderAccount, {select: true});
                let spendTxResult = await aeSdk.aensUpdate(name, pointers, {});
                this.responseSucess("aeAensUpdate", {hash: spendTxResult.hash});
            } catch (e) {
                this.responseError("aeAensUpdate", e.message);
            }
        },

        aeAensClaim: async function (secretKey, name) {
            let publicKey;
            try {
                if (!(await this.getNameInfo(name))) {
                    this.responseError("aeAensClaim", "Name is registered");
                    return;
                }
                publicKey = getAddressFromPriv(secretKey);
                const senderAccount = new MemoryAccount({
                    keypair: {secretKey: secretKey, publicKey: publicKey},
                });
                await aeSdk.addAccount(senderAccount, {select: true});
                const preClaimTx = await aeSdk.aensPreclaim(name);
                const claimTx = await aeSdk.aensClaim(name, preClaimTx.salt, {
                    verify: true,
                    waitMined: false,
                });

                this.responseSucess("aeAensClaim", {hash: claimTx.hash});
            } catch (e) {
                this.responseError("aeAensClaim", e.message);
            }
        },

