        aeAex9TokenCreateAllowance: async function (
            secretKey,
            ctAddress,
            forAddress,
            amount
        ) {
            try {
                forAddress = forAddress.toString().replace("ct_", "ak_");
                const publicKey = getAddressFromPriv(secretKey);
                const senderAccount = new MemoryAccount({
                    keypair: {secretKey: secretKey, publicKey: publicKey},
                });
                await aeSdk.addAccount(senderAccount, {select: true});
                let aex9Contract = await aeSdk.getContractInstance({
                    aci: Aex9TokenContract,
                    contractAddress: ctAddress,
                });

                let tx = await aex9Contract.methods.create_allowance(
                    forAddress,
                    toAettos(amount)
                );

                this.responseSucess("aeAex9TokenCreateAllowance", {
                    hash: tx.hash,
                });
            } catch (e) {
                if (e.message.toString().includes("ALLOWANCE_ALREADY_EXISTENT")) {
                    console.log("ALLOWANCE_ALREADY_EXISTENT");
                    try {
                        forAddress = forAddress.toString().replace("ct_", "ak_");
                        const publicKey = getAddressFromPriv(secretKey);
                        const senderAccount = new MemoryAccount({
                            keypair: {secretKey: secretKey, publicKey: publicKey},
                        });
                        await aeSdk.addAccount(senderAccount, {select: true});
                        let aex9Contract = await aeSdk.getContractInstance({
                            aci: Aex9TokenContract,
                            contractAddress: ctAddress,
                        });

                        let tx = await aex9Contract.methods.change_allowance(
                            forAddress,
                            toAettos(amount)
                        );

                        this.responseSucess("aeAex9TokenCreateAllowance", {
                            hash: tx.hash,
                        });
                    } catch (e) {
                        this.responseError("aeAex9TokenCreateAllowance", e.message);
                    }
                } else {
                    this.responseError("aeAex9TokenCreateAllowance", e.message);
                }
            }
        },
